import React, { useState, useEffect } from 'react';
import axios from 'axios';
import QuestionItem from '../Components/QuestionItem';

export default function Tagged(props){
    const tag = props.match.params.tag;
    const [tquestions, setTquestions] = useState([]);

useEffect(()=>{
    axios.get(`https://api.stackexchange.com/2.2/search?order=desc&sort=activity&tagged=${tag}&site=stackoverflow&filter=!9_bDDxJY5&key=eZoZLL*)llKhER8pMMusAw((`)
          .then((resp) => {
            setTquestions(resp.data.items);
          })
},[tag])

    return(
        <div>
            <h1>tagged</h1>
            {tquestions.map((question, index)=>{
                  return(
                    <QuestionItem question={question} key={index} />
                  )
                })}
        </div>
    )
}