import React, { useState, useEffect } from 'react';
import axios from 'axios';

export default function TagWiki(props){
    const tagname = props.names.replace('#', '%23');
    const tags = props.tags;
    const [tagsWiki, setTagsWiki] = useState([]);
    console.log(tagname)
    useEffect(() => {
        axios.get(`https://api.stackexchange.com/2.2/tags/${tagname}/wikis?site=stackoverflow&filter=!9_bDDrGXY&key=eZoZLL*)llKhER8pMMusAw((`)
        .then((resp)=>{
            setTagsWiki(resp.data.items);
        })
    }, [tagname])
    console.log(tagsWiki);
    return(
        tags.map((tag, index)=>{
            return(
                <div className='tag-body' key={index} onClick={() => window.location=`#/tagged/`+tag.name} >
                    <p className='tag-name'>{tag.name}</p>
                    <p className='tag-count'>{tag.count}</p>
                    {tagsWiki.length ? tagsWiki.map((wiki)=>{
                        if(wiki.tag_name == tag.name) return(<p className='tag-excerpt'>{wiki.excerpt}</p>)
                    }): ''}
                </div>
            )
        })
    )
}